// Modal ce credits
// --------------------------------------------------------
.modalCe {
    background-color: #005392 !important;
    padding: 20px;
    border: 1px solid #888;
}
.modaal-content-container {
    padding: 4px !important;
    text-align: center !important;
}
.modaal-title {
    margin-top: 20px;
    font-size: 19px;
    font-family: Montserrat,sans-serif;
    color: #fff;
    text-align: center;
    text-decoration: underline;
}
.modaal-text {
    font-size: 15px;
    font-family: Montserrat,sans-serif;
    color: #fff;
    text-align: center;
}

.btn-ce-text {
    font-family: Montserrat,sans-serif;
    background-color: #82a502;
    font-size: 14px;
    border-radius: 4px;
    padding: 10px 24px;
    text-transform: none!important;
    &:hover {
        box-shadow: 0 12px 16px 0 rgba(0,0,0,.24),0 17px 50px 0 rgba(0,0,0,.19);
    }
}

.btn-ce-no {
    font-family: Montserrat,sans-serif;
    color:white;
    cursor:pointer;
    background-color: grey;
    font-size: 14px;
    border-radius: 4px;
    font-weight: 400;
    margin-bottom: 4px;
    padding: 12px 24px;
    &:hover {
        box-shadow: 0 12px 16px 0 rgba(0,0,0,.24),0 17px 50px 0 rgba(0,0,0,.19);
    }
}

@media (max-width:991px) {
    .btn-ce-text {
        padding-left: 2px;
        padding-right: 2px;
        font-size: 10px;
    }
    .btn-ce-modaal {
        padding-left: 2px;
        padding-right: 2px;
        font-size: 10px;
    }
    .btn-ce-no {
        padding-left: 2px;
        padding-right: 2px;
        font-size: 10px;
    }

}
@media (max-width:600px) {
    .button-close-modaal {
        margin-right: -20%;
        margin-top: -10%;
    }
}
