.tooltip {
  padding: 10px;
  color: #333333;
  background-color: #ffffff;
  font-weight: normal;
  font-size: 13px;
  border-radius: 5px;
  position: absolute;
  z-index: 99999999;
  box-sizing: border-box;
  box-shadow: 0 1px 2px #000000;
  visibility: hidden;
  opacity: 0;
  transition: opacity 0.8s; }
  .tooltip i {
    position: absolute;
    width: 24px;
    height: 12px;
    overflow: hidden; }
    .tooltip i::after {
      content: '';
      position: absolute;
      width: 12px;
      height: 12px;
      background-color: #ffffff;
      box-shadow: 0 1px 2px #000000; }
  .tooltip.top {
    top: -10px;
    left: 50%;
    transform: translate(-50%, -100%); }
    .tooltip.top i {
      top: 100%;
      left: 50%;
      margin-left: -12px; }
      .tooltip.top i::after {
        left: 50%;
        transform: translate(-50%, -50%) rotate(45deg); }
  .tooltip.left {
    top: 50%;
    right: 100%;
    margin-right: 10px;
    transform: translate(0, -50%); }
    .tooltip.left i {
      top: 50%;
      left: 100%;
      margin-top: -12px;
      height: 24px; }
      .tooltip.left i::after {
        left: 0;
        top: 50%;
        transform: translate(-50%, -50%) rotate(-45deg); }
  .tooltip.bottom {
    top: 45px;
    left: 50%;
    transform: translate(-50%, 0); }
    .tooltip.bottom i {
      bottom: 100%;
      left: 50%;
      margin-left: -12px;
      height: 12px; }
      .tooltip.bottom i::after {
        left: 50%;
        transform: translate(-50%, 50%) rotate(45deg); }
  .tooltip.right {
    top: 50%;
    left: 100%;
    margin-left: 10px;
    transform: translate(0, -50%); }
    .tooltip.right i {
      top: 50%;
      right: 100%;
      margin-top: -12px;
      width: 12px;
      height: 24px; }
      .tooltip.right i::after {
        width: 12px;
        height: 12px;
        left: 0;
        top: 50%;
        transform: translate(50%, -50%) rotate(-45deg); }
  .tooltip.show {
    visibility: visible;
    opacity: 1; }

